body {
  font-family: 'Roboto', sans-serif;
}
.todo {
  height: 70px;
}
.todo-text {
  flex-grow: 1;
}
.todo-edit {
  flex-grow: 1;
}
.todo.completed .todo-text {
  text-decoration: line-through;
  opacity: 0.5;
}
.todo.completed .fa-check-square {
  opacity: 0.5;
}
.todo .todo-app .table {
  text-align: right;
  margin-bottom: 0;
  width: auto;
}
.todo-app {
  overflow: hidden;
}
.todo-app .table th,
.todo-app .table td {
  padding-top: 0;
  padding-bottom: 0;
}
.todo-app .stopwatch-time {
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 2px;
}
.slide-enter {
  transform: translateX(-100%);
}
.slide-enter-active {
  transform: translateX(0);
  transition: all 300ms;
}
.slide-appear {
  opacity: 0;
}
.slide-appear-active {
  opacity: 1;
  transition: all 700ms;
}
